<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process272"></a>

<p><br></p>
<p><span class="rvts17">支线：林德·凯姆藏宝室（</span><span class="rvts50">The Vault of Linder Kemm</span><span class="rvts17">）</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:146,Y:306</span><span class="rvts21">，地点：林德</span><span class="rvts20"></span><span class="rvts21">凯姆藏宝室</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">发现藏宝室，并探索藏宝室</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1495.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts77">该任务可以结合</span><span class="rvts20"></span><span class="rvts21">寻找阿虎大人</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">越老越值钱</span><span class="rvts20"></span><span class="rvts21">支线共同进行。林德</span><span class="rvts20"></span><span class="rvts21">凯姆的藏宝室位于林德的后花园，在这里可以找到一处隐藏的闸刀，使用解锁技能拉下闸刀，可以激活不远处的地下室舱门。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1496.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts77">这里是舱门的位置。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1497.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts77">探索林德</span><span class="rvts20"></span><span class="rvts21">凯姆藏宝室，其中包括</span><span class="rvts20"></span><span class="rvts21">越来越值钱</span><span class="rvts20"></span><span class="rvts21">支线中的三件古董，和图片中囚禁阿虎大人的秘门。该秘门屏风需要从下水道中的一处隐藏房间内，说服一个小孩（或打败小孩的</span><span class="rvts20">3</span><span class="rvts21">个巨魔）来获得，详情参考</span><span class="rvts20"></span><span class="rvts21">寻找阿虎大人</span><span class="rvts20"></span><span class="rvts21">支线。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1498.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts77">进入最终的秘密地下室，这里囚禁着被抓的阿虎大人。之后，释放阿虎大人，变回找来林德</span><span class="rvts20"></span><span class="rvts21">凯姆。他会把他是黑环同党的事情公众于世，该支线到此关闭。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



